@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
*{margin:0;padding:0}body{background-color:#0c191f;overflow-x:hidden}.app{width:100%}.app h2{text-align:center}.video-overlay{position:absolute;top:0;left:0;width:100vw;height:85vh;background-color:rgba(20,92,50,0.4)}.video-overlay h1{position:relative;top:52%;left:65%;font-size:2.6rem;text-shadow:0px 1px 0px #7f7f7f}.video-overlay h2{position:relative;top:44%;right:19rem;font-size:2.6rem;text-shadow:0px 1px 0px #7f7f7f}.video-first{width:100%;height:85vh;object-fit:cover;z-index:1}.home{background-color:#1f2533}.container{width:96.65vw;position:relative;margin-left:1rem;background-image:url(../../static/media/bubble2.9f1bbe25.jpg);background-repeat:no-repeat;background-size:contain;opacity:0.6;z-index:2}.container h2{margin-left:1rem;font-size:2.5em;margin-bottom:20px;text-shadow:0 1px 0px #378ab4, 1px 0 0px #5dabcd, 1px 2px 1px #378ab4, 2px 1px 1px #5dabcd, 2px 3px 2px #378ab4, 3px 2px 2px #5dabcd, 3px 4px 2px #378ab4, 4px 3px 3px #5dabcd, 4px 5px 3px #378ab4, 5px 4px 2px #5dabcd, 5px 6px 2px #378ab4, 6px 5px 2px #5dabcd, 6px 7px 1px #378ab4, 7px 6px 1px #5dabcd, 7px 8px 0px #378ab4, 8px 7px 0px #5dabcd;color:#f8f2f2}.container h4{color:white;padding-left:2rem;margin-bottom:-0.7rem}.container p{margin:1.75rem;font-size:1em;color:#fff;font-weight:bold}.banner{position:fixed;top:9rem;right:35.9%;background-color:rgba(0,0,0,0.8);width:21rem;height:21rem;-webkit-clip-path:circle(50% at 50% 50%);clip-path:circle(50% at 50% 50%);padding:0.5rem}.banner video{position:relative;object-fit:cover;width:21rem;height:21rem;-webkit-clip-path:circle(50% at 50% 50%);clip-path:circle(50% at 50% 50%)}.hero{display:flex;justify-content:center;align-items:center;position:absolute;z-index:0}.middle-part{height:25rem;width:97vw;background-image:url(../../static/media/bubble1cut.51f69b6a.jpg);background-size:cover;color:black;text-align:center;border:#07221e 14px solid}.middle-part h2{display:flex;flex-direction:row;align-items:center;justify-content:center;margin:1rem 0 0 1.5rem;color:#049470;font-size:2rem;letter-spacing:0.8rem;text-shadow:1px 1px 2px #fff;transition:transform 300ms ease-in-out;pointer-events:none;filter:blur(4px);-webkit-filter:blur(2.5px);-webkit-animation:fadeIn 3s infinite alternate;animation:fadeIn 3s infinite alternate}@-webkit-keyframes fadeIn{from{opacity:0.19}}@keyframes fadeIn{from{opacity:0.19}}.btns{margin-right:0.3em;padding:0.5rem;color:#393b44;border:1px solid #393b44;border-radius:0.2rem}.btns:hover{box-shadow:1px 3px 9px 2px rgba(0,0,0,0.25)}.add{background-color:#d6e0f0}.remove{background-color:#e7c9ab;margin-top:1rem}.proceed{color:#cecaca;background-color:#2f504d;margin-top:1rem}.not-found{height:80vh;display:flex;flex-direction:column;justify-content:center;align-items:center}

.cart{min-height:78vh;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:1rem;position:relative;z-index:5;background-color:rgba(42,49,66,0.6);border:#07221e 14px solid}.cart h2{color:#93b4b4;margin-top:-17rem;font-size:2.3rem;margin-left:2rem}.cart-details{display:flex;margin:2rem 0 0 2rem;padding:1rem;background-color:rgba(243,244,247,0.2)}.cart-item{width:38rem;margin-bottom:1rem;padding:1rem;background-color:rgba(243,244,247,0.2)}.cart-item h3{margin-bottom:1rem}p{padding:0.5rem 0}.cart-actions{width:8rem;display:flex;flex-direction:column}.qty{width:3rem;margin-left:0.5rem}.cart-summary h4{margin-bottom:1rem;font-weight:bold}

.products{position:relative;z-index:5;background-color:rgba(42,49,66,0.6);border:#07221e 14px solid}.product{max-width:80vw;display:flex;margin:2rem auto;padding:1rem;background-color:rgba(113,113,167,0.7);box-shadow:1px 3px 9px 2px rgba(0,0,0,0.25)}.products h2{margin-left:2rem;padding-top:1.3rem;color:#93b4b4;font-size:2.2rem}.product-info{width:30rem}.product-btns{align-self:end;margin-left:auto}.product-img{min-width:150px;min-height:180px;margin:0 1rem 1rem 1rem;object-fit:contain}.product h3{padding-bottom:1.5rem}.price{margin-top:1rem}.single-cart-product-container{min-height:75vh;background-color:rgba(148,148,161,0.5);position:relative;z-index:5;margin:1rem 0}.single-cart-product-container h2{padding-top:1rem}.single-cart-product{width:80rem;margin:2rem auto;background-color:rgba(82,82,110,0.6)}.img-div{width:300rem;margin:1rem;margin-top:0}.img-div img{min-height:100%;min-width:100%}.single-cart-product .price{margin-top:0rem}.add-cart{margin-top:0.9rem}.all{color:#413838;margin-left:2rem;background-color:#91aac0;border-radius:7px;cursor:pointer}

.auth{margin:1rem auto;height:68vh;display:flex;flex-direction:column;justify-content:center;align-items:center;position:relative;z-index:5;background-color:rgba(33,33,49,0.5)}.auth h2{color:#93b4b4;margin-top:-3rem;font-size:2.2rem;margin-left:2rem}form{color:white;width:30rem;display:flex;flex-direction:column;padding:1rem}form fieldset{width:20rem;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:3rem auto;padding:1rem;border-radius:0.3em}form label{display:flex;flex-direction:column}label input{margin:0.5rem 0}.auth-user{display:flex;flex-direction:column;justify-content:center;align-items:center;line-height:1.5em}.auth-user h3{padding:2rem 0 1rem 0}.login-btn{background-color:#cac6e4;margin-top:1rem;padding:0.5rem 2rem}.logout-btn{background-color:#e7c9ab;margin-top:2rem}

footer{display:flex;justify-content:center;align-items:center;justify-content:center;font-family:'Ubuntu', sans-serif;letter-spacing:0.03em;font-weight:bolder;color:#f1f3f8;border:#0e1b24 14px solid}footer .footer-container{width:100vw;height:18rem}footer .video-footer{width:100%;height:100%;object-fit:cover}footer .footer-text{position:relative;padding:2rem;top:-13rem;font-size:large;color:black}footer .footer-text p{margin-top:3rem}

.nav{position:fixed;width:100vw;background-image:url(../../static/media/bubbleGreen1.3831a625.png);opacity:0.85;z-index:3;border-bottom:#750101 1px solid}.nav ul{display:flex;justify-content:space-around;align-items:center}.nav ul li{list-style:none}.nav ul a{text-decoration:none;color:#fffdfd;font-size:1.1rem;margin:12px 10px;padding:10px 10px;position:relative;font-weight:bold;letter-spacing:0.02rem}.nav ul .nav-link-cart{display:flex}.nav ul .nav-link-cart p{align-self:start;margin:0;padding:0 0.5rem}.nav ul .nav-link-cart p:first-child{padding:0}.nav ul.container-blue-circleBehind a:before,.nav ul.container-blue-circleBehind a:after{position:absolute;top:22px;left:50%;width:50px;height:50px;border:3px solid #4f8d40;transform:translateX(-50%) translateY(-50%) scale(0.8);border-radius:50%;background:transparent;content:"";opacity:0;transition:all 0.3s;z-index:-1}.nav ul.container-blue-circleBehind a:after{border-width:2px;transition:all 0.4s}.nav ul.container-blue-circleBehind a:hover:before{opacity:0.7;transform:translateX(-50%) translateY(-50%) scale(1)}.nav ul.container-blue-circleBehind a:hover:after{opacity:0.7;transform:translateX(-50%) translateY(-50%) scale(1.3)}


